import React, { useState } from 'react'
import Button from '@mui/material/Button'
import Menu from '@mui/material/Menu'
import MenuItem from '@mui/material/MenuItem'
import PublicIcon from '@mui/icons-material/Public'
import i18n from 'i18next'

const LangMenu = () => {
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null)
  const [language, setLanguage] = useState(navigator.language.split('-')[0])
  const open = Boolean(anchorEl)

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  const changeLang = async (e: React.MouseEvent<HTMLLIElement, MouseEvent>) => {
    let lang = e.currentTarget.innerText
    lang === '' ? lang = 'ja' : lang = 'en'

    setLanguage(lang)
    await i18n.changeLanguage(lang).finally(() => {
      language === 'ja' ? setLanguage(lang) : setLanguage(lang)
      handleClose()
    })
  }

  return (
    <div>
      <Button
        id="basic-button"
        aria-controls={open ? 'basic-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}
      >
        <PublicIcon fontSize='small' className='ml-1 icon' />
      </Button>
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{ 'aria-labelledby': 'basic-button' }}
      >
        <MenuItem
          onClick={(e) => { changeLang(e).catch(() => null) }}
          disabled={language === 'en'}
        >
          English
        </MenuItem>
        <MenuItem
          onClick={(e) => { changeLang(e).catch(() => null) }}
          disabled={language === 'ja'}
        >
          
        </MenuItem>
      </Menu>
    </div>
  )
}

export default LangMenu
